.popup__button-inprogress_enabled {
    display: block;
    width: 358px;
    height: 50px;
    background-color: #000000;
    color: #ffffff;
    font-style: normal;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    margin-top: 18px;
    margin-bottom: 37px;
    padding: 0;
    border: 0;
    border-radius: 2px;
    opacity: 1;
    transition: opacity 0.8s linear;
}

@media (max-width: 760px) {
    .popup__button-inprogress_enabled {
        width: 238px;
        font-size: 14px;
        line-height: 17px;
        padding: 0;
        margin-top: 15px;
        margin-bottom: 25px;
    }
}